import React, {useState} from "react";
import {HashRouter as Router, Route , Switch} from 'react-router-dom';
import { Link } from "react-router-dom";



const Navbar =() =>{
  const[navLinkOpen,navLinkToggle]=useState(false);
  const handleNavLinksToggle=()=>{
    navLinkToggle(!navLinkOpen)
  }

  const renderClasses=() =>{
    let classes ="navlinks"
    if(navLinkOpen){
      classes +=" active"
    }
    return classes
  }
  return <nav>
  <div className="logo">
  <i className="fas fa-mountain"></i>
<h4>Steins Gate</h4>
</div>
<ul className={renderClasses()}>
<li className ="link"><Link to="/">Home</Link></li>
<li className ="link"><Link to="/Characters">Lab Members</Link></li>
<li className ="link"><Link to="/WatchOrder">Watch Order</Link></li>
<li className ="link"><Link to="/ImageGallery">Image Gallery</Link></li>
<li className ="link"><Link to="/VideoClips">Video Clips</Link></li>
</ul>
<div onClick={handleNavLinksToggle} className="hamburger-toggle">
<i className=" fas fa-bars fa-lg"></i>
</div>
</nav>;
}

export default Navbar
